<template lang="html">
  <div class="sign-up-zhihu">
    <div class="slogan">
      <h3>加入知乎</h3>
      <p>与世界分享你的知识、经验和见解</p>
    </div>
    <form class="signup-form" action="index.html" method="post">
      <ul>
        <li><input type="text" class="name" placeholder="姓名"></input></li>
        <li><input type="text" class="phone" placeholder="手机号"></input></li>
        <li><input type="password" class="password" placeholder="密码（不少于8位）"></input></li>
      </ul>
      <a class="signup-btn">注册</a>
      <p>已有账号？<a href="#" class="login">登录</a></p>
    </form>
  </div>
</template>

<script>
export default {
}
</script>

<style lang="scss" scoped>
  .sign-up-zhihu {
    border: 1px solid #ddd;
    border-radius: 2px;
    margin: 10px;
    .slogan {
      height: 100px;
      padding-top: 30px;
      h3, p {
        text-align: center;
        color: #777;
      }
    }
    .signup-form {
      li {
        border-top: 1px solid #ddd;
        height: 50px;
      }
      li:last-of-type {
        border-bottom: 1px solid #ddd;
      }
      input {
        width: 100%;
        height: 48px;
        border: none;
        padding: 10px;
      }
      a.signup-btn {
        // background: #06c;
        background: linear-gradient(#06c, #05b);
        text-align: center;
        color: #eee;
        height: 34px;
        line-height: 34px;
        margin: 6px;
        border-radius: 2px;
        // border: 1px solid #07d;
        box-shadow: 0 1px 5px #000;
      }
      a.login {
        display: inline;
        color: #06c;
      }
      p {
        margin: 5px;
        color: #777;
      }
    }
  }
</style>



